.modal_wrapper {
  width: 100%;
  height: 100%;
  position: asbolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.modal_wrapper::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal_content {
  background-color: #fff;
  width: 600px;
  height: 500px;
  margin: 4rem auto;
  padding: 2rem;
}

.modal_heading {
  color: #042f75;
  font-weight: bolder;
  font-size: 1.2rem;
}
